import { Notify } from 'notiflix/build/notiflix-notify-aio';
import { Loading } from 'notiflix/build/notiflix-loading-aio';
import { Report } from 'notiflix/build/notiflix-report-aio';
import { fetchTrending } from './fetchTrending';

import { fetchGenres } from '../fetchGenres';

const refs = {
  searchForm: document.querySelector('.searchForm'),
  searchQueryInput: document.querySelector('input[name=searchQuery]'),
  searchBtn: document.querySelector('.searchForm__button'),
  gallery: document.querySelector('.gallery'),
};

let page = 1;
let pageLimit = 40;
let searchQuery = '';
let gallery;
let lastCard;

const renderMarkup = async () => {
  try {
    const { page, results, total_pages, total_results } = await fetchTrending();
    const { genres } = await fetchGenres();

    if (total_results > 0) {
      Loading.hourglass();

      refs.gallery.innerHTML = galleryMarkupreation(results, genres);

      if (total_results > pageLimit) {
      }

      Loading.remove();
      return;
    }
    Report.failure('Sorry, some problem happend. Please try again.');
    refs.gallery.innerHTML = '';
  } catch (error) {
    // eslint-disable-next-line no-console
    console.error(error.message);
  }
};

renderMarkup();

const cardGenres = (genre_ids, genres) => {
  let arr = [];

  genre_ids.map(genre_id =>
    genres.map(item => {
      if (item.id === genre_id) {
        arr.push(item.name);
      }
    })
  );

  return arr.join(', ');
};

const galleryMarkupreation = (results, genres) => {
  const markup = results
    .map(
      ({ poster_path, title, genre_ids, release_date }) =>
        `
        <li class="movieCard" data-modal-open>
            <article>
                <a>
                    <img class="movieCard__image" src="https://image.tmdb.org/t/p/w500${poster_path}" alt="movieImg" />
                    <div class="movieCard__info">
                        <p class="movieCard__title">${title}</p>
                        <p class="movieCard__description">${cardGenres(
                          genre_ids,
                          genres
                        )} | ${release_date}</p>
                    </div>
                </a>
            </article>
        </li>
      `
    )
    .join('');
  return markup;
};
